import React from 'react';
import type { MediaFile } from '../../../types/media';
import { MediaTile } from './MediaTile';

interface MediaGridViewProps {
    files: MediaFile[];
    selectionMode: boolean;
    selectedFileId: string | null;
    onFileClick: (file: MediaFile, index: number) => void;
    onDownload: (file: MediaFile) => void;
    onDeleteRequest: (file: MediaFile) => void;
    onKeyDown: (e: React.KeyboardEvent, index: number) => void;
    setFileRef: (index: number) => (el: HTMLDivElement | null) => void;
    containerRef: React.RefObject<HTMLDivElement | null>;
}

export const MediaGridView: React.FC<MediaGridViewProps> = ({
    files,
    selectionMode,
    selectedFileId,
    onFileClick,
    onDownload,
    onDeleteRequest,
    onKeyDown,
    setFileRef,
    containerRef,
}) => {
    return (
        <div ref={containerRef} className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 p-1">
            {files.map((file, index) => (
                <MediaTile
                    key={file.id}
                    file={file}
                    index={index}
                    selectionMode={selectionMode}
                    isSelected={selectedFileId === file.id}
                    onFileClick={onFileClick}
                    onDownload={onDownload}
                    onDeleteRequest={onDeleteRequest}
                    onKeyDown={onKeyDown}
                    setRef={setFileRef(index)}
                />
            ))}
        </div>
    );
};
