import { showSnackbar } from 'ether-ui';
import React, { useEffect, useRef, useState } from 'react';
import type { MediaFile } from '../../../types/media';
import { getCurrentAuthToken } from '../../services/auth';
import { useMediaStore } from '../../services/media';
import { ConfirmationModal } from '../ConfirmationModal';
import FileUpload from '../FileUpload';
import { Button, Dropdown, TextInput } from '../index';
import { MediaGridView } from './MediaGridView';
import { MediaListView } from './MediaListView';

type ViewMode = 'grid' | 'tile';

interface MediaGridProps {
    selectionMode?: boolean;
    onFileSelect?: (file: MediaFile) => void;
    fileType?: 'image' | 'video' | 'document';
    showUpload?: boolean;
    showViewToggle?: boolean;
    showSearch?: boolean;
    showFilter?: boolean;
}

const allowedFileExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.webp', '.mp4', '.mov', '.avi', '.pdf', '.docx', '.doc', '.txt', '.csv'];

const maxFileSize = 50 * 1024 * 1024;

const fileTypeOptions = [
    { label: 'All Types', value: '' },
    { label: 'Images', value: 'image' },
    { label: 'Videos', value: 'video' },
    { label: 'Documents', value: 'document' },
    { label: 'CSV', value: 'csv' },
];

export const MediaGrid: React.FC<MediaGridProps> = ({
    selectionMode = false,
    onFileSelect,
    fileType,
    showUpload = false,
    showViewToggle = false,
    showSearch = true,
    showFilter = false,
}) => {
    const { files, isLoading, loadFiles, deleteFile } = useMediaStore();
    const [viewMode, setViewMode] = useState<ViewMode>('grid');
    const [searchQuery, setSearchQuery] = useState('');
    const [filterType, setFilterType] = useState<string>('');
    const [selectedFileId, setSelectedFileId] = useState<string | null>(null);
    const [fileToDelete, setFileToDelete] = useState<MediaFile | null>(null);
    const [focusedIndex, setFocusedIndex] = useState(0);
    const containerRef = useRef<HTMLDivElement>(null);
    const fileRefs = useRef<(HTMLDivElement | null)[]>([]);

    useEffect(() => {
        loadFiles({ search: searchQuery, previewType: fileType || filterType });
    }, [searchQuery, filterType, fileType]);

    useEffect(() => {
        fileRefs.current = fileRefs.current.slice(0, files.length);
    }, [files]);

    useEffect(() => {
        if (focusedIndex >= 0 && focusedIndex < files.length) {
            fileRefs.current[focusedIndex]?.focus();
        }
    }, [focusedIndex, files.length]);

    const handleUploadComplete = () => {
        loadFiles({ search: searchQuery, previewType: fileType || filterType });
    };

    const handleDeleteRequest = (file: MediaFile) => {
        setFileToDelete(file);
    };

    const handleDeleteConfirm = async () => {
        try {
            if (fileToDelete) {
                await deleteFile(fileToDelete.id);
                loadFiles({ search: searchQuery, previewType: fileType || filterType });
                setFileToDelete(null);
            }
        } catch {
            showSnackbar('Unable to delete selected files', 'Delete failed');
        }
    };

    const handleDownload = (file: MediaFile) => {
        window.open(`/api/files/${file.id}/download`, '_blank');
    };

    const getColumnsCount = () => {
        if (!containerRef.current) return 4;
        const width = containerRef.current.offsetWidth;
        if (width >= 1024) return 4;
        if (width >= 768) return 3;
        return 2;
    };

    const handleKeyDown = (e: React.KeyboardEvent, index: number) => {
        const file = files[index];

        switch (e.key) {
            case 'ArrowRight':
                e.preventDefault();
                if (viewMode === 'grid') {
                    setFocusedIndex(Math.min(files.length - 1, index + 1));
                }
                break;
            case 'ArrowLeft':
                e.preventDefault();
                if (viewMode === 'grid') {
                    setFocusedIndex(Math.max(0, index - 1));
                }
                break;
            case 'ArrowDown':
                e.preventDefault();
                if (viewMode === 'grid') {
                    const cols = getColumnsCount();
                    const newIndex = Math.min(files.length - 1, index + cols);
                    setFocusedIndex(newIndex);
                } else {
                    setFocusedIndex(Math.min(files.length - 1, index + 1));
                }
                break;
            case 'ArrowUp':
                e.preventDefault();
                if (viewMode === 'grid') {
                    const cols = getColumnsCount();
                    const newIndex = Math.max(0, index - cols);
                    setFocusedIndex(newIndex);
                } else {
                    setFocusedIndex(Math.max(0, index - 1));
                }
                break;
            case 'Enter':
                e.preventDefault();
                if (selectionMode && onFileSelect) {
                    onFileSelect(file);
                } else {
                    handleDownload(file);
                }
                break;
            case 'Delete':
                e.preventDefault();
                if (!selectionMode) {
                    handleDeleteRequest(file);
                }
                break;
        }
    };

    const handleFileClick = (file: MediaFile, index: number) => {
        setFocusedIndex(index);
        if (selectionMode) {
            setSelectedFileId(file.id);
            if (onFileSelect) {
                onFileSelect(file);
            }
        }
    };

    const setFileRef = (index: number) => (el: HTMLDivElement | null) => {
        fileRefs.current[index] = el;
    };

    return (
        <div className="space-y-6">
            {showUpload && (
                <FileUpload
                    uploadUrl="/api/files/upload"
                    getAuthToken={getCurrentAuthToken}
                    allowedExtensions={allowedFileExtensions}
                    maxFileSize={maxFileSize}
                    onUploadComplete={handleUploadComplete}
                />
            )}

            {(showSearch || showFilter || showViewToggle) && (
                <div className="flex items-center gap-4">
                    {showSearch && (
                        <TextInput
                            placeholder="Search files..."
                            value={searchQuery}
                            onChange={(e: any) => setSearchQuery(e.value)}
                            className="flex-1"
                        />
                    )}
                    {showFilter && (
                        <div className="min-w-50">
                            <Dropdown options={fileTypeOptions} value={filterType} onChange={(e: any) => setFilterType(e.value)} />
                        </div>
                    )}
                    {showViewToggle && (
                        <div className="flex gap-2 shrink-0">
                            <Button variant={viewMode === 'grid' ? 'primary' : 'default'} size="sm" onClick={() => setViewMode('grid')}>
                                Grid
                            </Button>
                            <Button variant={viewMode === 'tile' ? 'primary' : 'default'} size="sm" onClick={() => setViewMode('tile')}>
                                List
                            </Button>
                        </div>
                    )}
                </div>
            )}

            {isLoading ? (
                <div className="text-center py-12">Loading...</div>
            ) : files.length === 0 ? (
                <div className="text-center py-12 text-gray-500">No files found</div>
            ) : viewMode === 'grid' ? (
                <MediaGridView
                    files={files}
                    selectionMode={selectionMode}
                    selectedFileId={selectedFileId}
                    onFileClick={handleFileClick}
                    onDownload={handleDownload}
                    onDeleteRequest={handleDeleteRequest}
                    onKeyDown={handleKeyDown}
                    setFileRef={setFileRef}
                    containerRef={containerRef}
                />
            ) : (
                <MediaListView
                    files={files}
                    selectionMode={selectionMode}
                    selectedFileId={selectedFileId}
                    onFileClick={handleFileClick}
                    onDownload={handleDownload}
                    onDeleteRequest={handleDeleteRequest}
                    onKeyDown={handleKeyDown}
                    setFileRef={setFileRef}
                />
            )}

            <ConfirmationModal
                isOpen={!!fileToDelete}
                onClose={() => setFileToDelete(null)}
                onConfirm={handleDeleteConfirm}
                title="Delete File"
                message={`Are you sure you want to delete "${fileToDelete?.originalName}"? This action cannot be undone.`}
                confirmText="Delete"
                variant="danger"
            />
        </div>
    );
};
