import classNames from 'classnames';
import { TimesIcon, UploadIcon } from 'ether-ui/icons';
import React, { useCallback, useRef, useState } from 'react';

export interface UploadingFile {
    id: string;
    file: File;
    progress: number;
    status: 'uploading' | 'completed' | 'error';
    error?: string;
}

interface FileUploadProps {
    uploadUrl: string;
    getAuthToken?: () => string | null;
    allowedExtensions?: string[];
    maxFileSize?: number;
    maxFiles?: number;
    onUploadComplete?: (files: any[]) => void;
    onUploadStart?: (files: File[]) => void;
    onUploadProgress?: (fileId: string, progress: number) => void;
    onUploadError?: (fileId: string, error: string) => void;
    className?: string;
    children?: React.ReactNode;
}

const validateFile = (file: File, maxFileSize: number, allowedExtensions?: string[]): string | null => {
    if (file.size > maxFileSize) {
        return `File size exceeds ${Math.round(maxFileSize / 1024 / 1024)}MB limit`;
    }

    if (allowedExtensions && allowedExtensions.length > 0) {
        const fileExtension = file.name.split('.').pop()?.toLowerCase();
        if (!fileExtension || !allowedExtensions.includes(`.${fileExtension}`)) {
            return `File type not allowed. Allowed types: ${allowedExtensions.join(', ')}`;
        }
    }

    return null;
};

const uploadFileToServer = (
    file: File,
    uploadUrl: string,
    authToken: string | null,
    onProgress: (progress: number) => void,
    abortSignal: AbortSignal
): Promise<any> => {
    return new Promise((resolve, reject) => {
        const formData = new FormData();
        formData.append('files', file);

        const xhr = new XMLHttpRequest();

        xhr.upload.addEventListener('progress', (event) => {
            if (event.lengthComputable) {
                const progress = Math.round((event.loaded * 100) / event.total);
                onProgress(progress);
            }
        });

        xhr.addEventListener('load', () => {
            if (xhr.status === 200) {
                const response = JSON.parse(xhr.responseText);
                resolve(response.files?.[0]);
            } else {
                try {
                    const response = JSON.parse(xhr.responseText);
                    reject(new Error(response.message || response.error || 'Upload failed'));
                } catch {
                    reject(new Error('Upload failed'));
                }
            }
        });

        xhr.addEventListener('error', () => {
            reject(new Error('Network error'));
        });

        abortSignal.addEventListener('abort', () => {
            xhr.abort();
            reject(new Error('Upload cancelled'));
        });

        xhr.open('POST', uploadUrl);
        if (authToken) {
            xhr.setRequestHeader('Authorization', `Bearer ${authToken}`);
        }
        xhr.send(formData);
    });
};

const FileUpload: React.FC<FileUploadProps> = ({
    uploadUrl,
    getAuthToken,
    allowedExtensions,
    maxFileSize = 50 * 1024 * 1024,
    maxFiles = 10,
    onUploadComplete,
    onUploadStart,
    onUploadProgress,
    onUploadError,
    className,
    children,
}) => {
    const [isDragOver, setIsDragOver] = useState(false);
    const [uploadingFiles, setUploadingFiles] = useState<UploadingFile[]>([]);
    const fileInputRef = useRef<HTMLInputElement>(null);
    const abortControllersRef = useRef<Map<string, AbortController>>(new Map());

    const uploadFile = async (file: File): Promise<any> => {
        const fileId = `${Date.now()}-${Math.random()}`;
        const abortController = new AbortController();
        abortControllersRef.current.set(fileId, abortController);

        const uploadingFile: UploadingFile = {
            id: fileId,
            file,
            progress: 0,
            status: 'uploading',
        };

        setUploadingFiles((prev) => [...prev, uploadingFile]);

        try {
            const authToken = getAuthToken ? getAuthToken() : null;

            const result = await uploadFileToServer(
                file,
                uploadUrl,
                authToken,
                (progress) => {
                    setUploadingFiles((prev) => prev.map((f) => (f.id === fileId ? { ...f, progress } : f)));
                    onUploadProgress?.(fileId, progress);
                },
                abortController.signal
            );

            setUploadingFiles((prev) => prev.map((f) => (f.id === fileId ? { ...f, status: 'completed', progress: 100 } : f)));
            return result;
        } catch (error) {
            const errorMessage = error instanceof Error ? error.message : 'Upload failed';
            setUploadingFiles((prev) => prev.map((f) => (f.id === fileId ? { ...f, status: 'error', error: errorMessage } : f)));
            onUploadError?.(fileId, errorMessage);
            throw error;
        } finally {
            abortControllersRef.current.delete(fileId);
        }
    };

    const handleFiles = useCallback(
        async (files: FileList) => {
            const fileArray = Array.from(files);

            if (fileArray.length > maxFiles) {
                alert(`Maximum ${maxFiles} files allowed`);
                return;
            }

            const validFiles: File[] = [];
            const errors: string[] = [];

            fileArray.forEach((file) => {
                const error = validateFile(file, maxFileSize, allowedExtensions);
                if (error) {
                    errors.push(`${file.name}: ${error}`);
                } else {
                    validFiles.push(file);
                }
            });

            if (errors.length > 0) {
                alert(`File validation errors:\n${errors.join('\n')}`);
            }

            if (validFiles.length === 0) return;

            onUploadStart?.(validFiles);

            const uploadPromises = validFiles.map(uploadFile);

            try {
                const results = await Promise.allSettled(uploadPromises);
                const uploadedFiles = results
                    .filter((result): result is PromiseFulfilledResult<any> => result.status === 'fulfilled')
                    .map((result) => result.value);

                if (uploadedFiles.length > 0) {
                    onUploadComplete?.(uploadedFiles);
                }

                setTimeout(() => {
                    setUploadingFiles((prev) => prev.filter((f) => f.status === 'uploading'));
                }, 3000);
            } catch (error) {
                console.error('Upload error:', error);
            }
        },
        [maxFiles, maxFileSize, allowedExtensions, onUploadStart, onUploadComplete, onUploadProgress, onUploadError, uploadFile]
    );

    const cancelUpload = (fileId: string) => {
        const abortController = abortControllersRef.current.get(fileId);
        if (abortController) {
            abortController.abort();
        }
    };

    const handleDragOver = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        if (!isDragOver) {
            setIsDragOver(true);
        }
    };

    const handleDragLeave = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        if (e.currentTarget === e.target) {
            setIsDragOver(false);
        }
    };

    const handleDrop = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragOver(false);

        const files = e.dataTransfer.files;
        if (files.length > 0) {
            handleFiles(files);
        }
    };

    const handleFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const files = e.target.files;
        if (files && files.length > 0) {
            handleFiles(files);
        }
        e.target.value = '';
    };

    const openFileDialog = () => {
        fileInputRef.current?.click();
    };

    return (
        <div className={className}>
            <div
                className={classNames('relative border-2 border-dashed rounded-lg transition-all duration-200', {
                    'border-blue-400 bg-blue-50': isDragOver,
                    'border-gray-300 hover:border-gray-400': !isDragOver,
                })}
                onDragOver={handleDragOver}
                onDragLeave={handleDragLeave}
                onDrop={handleDrop}
            >
                {!children && (
                    <div className="p-8 text-center">
                        <UploadIcon className="mx-auto h-12 w-12 text-gray-400 mb-4" />
                        <div className="space-y-2">
                            <p className="text-lg font-medium text-gray-900">Drop files here to upload</p>
                            <p className="text-sm text-gray-500">
                                or{' '}
                                <button
                                    type="button"
                                    onClick={openFileDialog}
                                    className="text-blue-600 hover:text-blue-700 font-medium underline"
                                >
                                    browse from your computer
                                </button>
                            </p>
                            {allowedExtensions && <p className="text-xs text-gray-400">Allowed: {allowedExtensions.join(', ')}</p>}
                            <p className="text-xs text-gray-400">Max size: {Math.round(maxFileSize / 1024 / 1024)}MB per file</p>
                        </div>
                    </div>
                )}

                {children}

                {uploadingFiles.length > 0 && (
                    <div className="absolute inset-0 bg-white bg-opacity-90 rounded-lg flex items-center justify-center">
                        <div className="w-full max-w-md p-4">
                            <h3 className="text-lg font-medium text-gray-900 mb-4">Uploading Files...</h3>
                            <div className="space-y-3">
                                {uploadingFiles.map((file) => (
                                    <div key={file.id} className="relative">
                                        <div className="flex items-center justify-between mb-1">
                                            <span className="text-sm text-gray-700 truncate">{file.file.name}</span>
                                            <div className="flex items-center space-x-2">
                                                <span className="text-xs text-gray-500">
                                                    {file.status === 'uploading' && `${file.progress}%`}
                                                    {file.status === 'completed' && 'Complete'}
                                                    {file.status === 'error' && 'Error'}
                                                </span>
                                                {file.status === 'uploading' && (
                                                    <button onClick={() => cancelUpload(file.id)} className="text-red-500 hover:text-red-700">
                                                        <TimesIcon className="h-4 w-4" />
                                                    </button>
                                                )}
                                            </div>
                                        </div>
                                        {file.status === 'uploading' && (
                                            <div className="w-full bg-gray-200 rounded-full h-2">
                                                <div
                                                    className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                                                    style={{ width: `${file.progress}%` }}
                                                />
                                            </div>
                                        )}
                                        {file.status === 'error' && file.error && <p className="text-xs text-red-600 mt-1">{file.error}</p>}
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                )}
            </div>

            <input
                ref={fileInputRef}
                type="file"
                multiple
                className="hidden"
                onChange={handleFileInputChange}
                accept={allowedExtensions?.join(',')}
            />
        </div>
    );
};

export default FileUpload;
