import classNames from 'classnames';
import { DownloadIcon, TrashIcon } from 'ether-ui/icons';
import React from 'react';
import type { MediaFile } from '../../../types/media';
import { Button } from '../index';

interface MediaListItemProps {
    file: MediaFile;
    index: number;
    selectionMode: boolean;
    isSelected: boolean;
    onFileClick: (file: MediaFile, index: number) => void;
    onDownload: (file: MediaFile) => void;
    onDeleteRequest: (file: MediaFile) => void;
    onKeyDown: (e: React.KeyboardEvent, index: number) => void;
    setRef: (el: HTMLDivElement | null) => void;
}

export const MediaListItem: React.FC<MediaListItemProps> = ({
    file,
    index,
    selectionMode,
    isSelected,
    onFileClick,
    onDownload,
    onDeleteRequest,
    onKeyDown,
    setRef,
}) => {
    return (
        <div
            ref={setRef}
            tabIndex={0}
            title={file.originalName}
            className={classNames(
                'flex items-center justify-between p-4 border rounded-lg cursor-pointer transition-all outline-none',
                'focus:ring-2 focus:ring-blue-500',
                {
                    'border-blue-500 bg-blue-50': selectionMode && isSelected,
                    'border-gray-200 hover:border-gray-300': !selectionMode || !isSelected,
                }
            )}
            onClick={() => onFileClick(file, index)}
            onKeyDown={(e) => onKeyDown(e, index)}
        >
            <div className="flex items-center gap-4">
                {file.previewType === 'image' ? (
                    <img src={`/api/files/${file.id}/download`} alt={file.originalName} className="w-16 h-16 object-cover rounded" />
                ) : (
                    <div className="w-16 h-16 bg-gray-100 rounded flex items-center justify-center">
                        <span className="text-gray-400 text-xs">{file.extension}</span>
                    </div>
                )}
                <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium truncate">{file.originalName}</p>
                    <p className="text-xs text-gray-500">
                        {(file.size / 1024).toFixed(2)} KB • {file.previewType} • Uploaded {new Date(file.uploadedAt).toLocaleDateString()}
                    </p>
                </div>
            </div>

            {!selectionMode && (
                <div className="flex gap-2 shrink-0" onClick={(e) => e.stopPropagation()}>
                    <Button
                        size="sm"
                        variant="primary"
                        layout="plain"
                        leftIcon={DownloadIcon}
                        onClick={(e) => {
                            e.stopPropagation();
                            onDownload(file);
                        }}
                    />
                    <Button
                        size="sm"
                        variant="danger"
                        layout="plain"
                        leftIcon={TrashIcon}
                        onClick={(e) => {
                            e.stopPropagation();
                            onDeleteRequest(file);
                        }}
                    />
                </div>
            )}
        </div>
    );
};
