import classNames from 'classnames';
import { DownloadIcon, TrashIcon } from 'ether-ui/icons';
import React from 'react';
import type { MediaFile } from '../../../types/media';
import { Button } from '../index';

interface MediaTileProps {
    file: MediaFile;
    index: number;
    selectionMode: boolean;
    isSelected: boolean;
    onFileClick: (file: MediaFile, index: number) => void;
    onDownload: (file: MediaFile) => void;
    onDeleteRequest: (file: MediaFile) => void;
    onKeyDown: (e: React.KeyboardEvent, index: number) => void;
    setRef: (el: HTMLDivElement | null) => void;
}

export const MediaTile: React.FC<MediaTileProps> = ({
    file,
    index,
    selectionMode,
    isSelected,
    onFileClick,
    onDownload,
    onDeleteRequest,
    onKeyDown,
    setRef,
}) => {
    return (
        <div
            ref={setRef}
            tabIndex={0}
            title={file.originalName}
            className={classNames(
                'border rounded-lg p-4 space-y-2 cursor-pointer transition-all outline-none',
                'focus:ring-2 focus:ring-blue-500',
                {
                    'border-blue-500 bg-blue-50': selectionMode && isSelected,
                    'border-gray-200 hover:border-gray-300': !selectionMode || !isSelected,
                    'hover:shadow-md': !selectionMode,
                }
            )}
            onClick={() => onFileClick(file, index)}
            onKeyDown={(e) => onKeyDown(e, index)}
        >
            {file.previewType === 'image' ? (
                <img src={`/api/files/${file.id}/download`} alt={file.originalName} className="w-full h-32 object-cover rounded" />
            ) : (
                <div className="w-full h-32 bg-gray-100 rounded flex items-center justify-center">
                    <span className="text-gray-400 text-sm">{file.extension}</span>
                </div>
            )}
            <p className="text-sm font-medium truncate">{file.originalName}</p>
            <p className="text-xs text-gray-500">{(file.size / 1024).toFixed(2)} KB</p>

            {!selectionMode && (
                <div className="flex gap-2" onClick={(e) => e.stopPropagation()}>
                    <Button
                        size="sm"
                        variant="primary"
                        layout="plain"
                        leftIcon={DownloadIcon}
                        onClick={(e) => {
                            e.stopPropagation();
                            onDownload(file);
                        }}
                    />
                    <Button
                        size="sm"
                        variant="danger"
                        layout="plain"
                        leftIcon={TrashIcon}
                        onClick={(e) => {
                            e.stopPropagation();
                            onDeleteRequest(file);
                        }}
                    />
                </div>
            )}
        </div>
    );
};
